const { create } = require('apisauce')

const baseURL = ''
const headers = {
  Accept: 'application/json',
}

const api = create({
  baseURL,
  headers,
})

export const ApiPost = async (path, data, h) => {
  h = {
    ...headers,
    h,
  }
  return await api.post(path, data, h)
}

export const ApiGet = async (path, h) => {
  h = {
    ...headers,
    h,
  }
  return await api.get(path, null, h)
}
