
export const Parsingobj = (s) => {
  const o = JSON.parse(s.params)
  let path = <></>
  if (typeof o.path === 'string')
    path = <div>{o.path}</div>

  let request = <></>
  if (typeof o.body === 'object' && typeof o.body.request === 'object') {
    request = <div>{JSON.stringify(o.body.request).replace(/,/g, ', ')}</div>
  } else {
    // console.log(o.body)
  }

  let response = <></>
  if (typeof o.body === 'object' && typeof o.body.response === 'object') {
    response = <div>{JSON.stringify(o.body.response).replace(/,/g, ', ')}</div>
  } else {
    // console.log(o.body)
  }
  return <>
    {path}{request}{response}
  </>
}