import { useEffect, useState } from "react"
import { ApiPost } from "../api"
import Navbar from "../navbar"
import { Parsingobj } from "./parsingobj"

const ENDPOINT = '/ws'
const client = new WebSocket(ENDPOINT)

export default function Logging() {
  const [dataItems, setData] = useState([])

  useEffect(() => {
    syncLog()
    client.onopen = () => {
      console.log('WebSocket Client Connected')
    }
    client.onmessage = (message) => {
      syncLog()
      console.log(message)
    }
  }, [])

  const syncLog = async () => {
    const h = {
      'Content-Type': 'application/json',
    }
    const b = {
      limit: '1000',
      to: 'latest',
    }
    const res = await ApiPost('/logging', b, h)
    if (res.ok === true) {
      console.log(JSON.stringify(res.data.data))
      setData(res.data.data)
    }
  }

  return (
    <>
      <Navbar />
      <div className="container pt-4">
      <h1 class="h2 mb-4">Log Aktivitas</h1>
        <div className="table-responsive">
          <table className="table">
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col">Tanggal</th>
                <th scope="col">Client Log</th>
                <th scope="col">Tgl Server</th>
                <th scope="col">Server Log</th>
              </tr>
            </thead>
            <tbody>{dataItems.map((val, idx) => {
              return (
                <tr key={idx}>
                  <th scope="row">{val.id}</th>
                  <td>{val.clienttanggal}</td>
                  <td><Parsingobj params={val.clientlog} /></td>
                  <td>{val.servertanggal}</td>
                  <td><Parsingobj params={val.serverlog} /></td>
                </tr>
              )
            })}
            </tbody>
          </table>
        </div>
      </div>
    </>
  )
}